.usetehniks {
  display: flex;
  flex-flow: column;
  justify-content: space-between;
}

.usetehniks h2 {
  margin-bottom: 10px;
  margin-left: 6px;
}

.usetehniks__items {
  display: flex;
  flex-flow: row wrap;
  align-items: center;
}

.usetehniks__item {
  display: flex;
  flex-flow: row nowrap;
  flex: 1;
  align-items: flex-end;
  min-height: 100px;
  justify-content: center;
}

.usetehniks__item p {
  margin: 56px 32px;
}